.mainContainer {
	position: relative;
	align-self: center;
	display: flex;
	flex-direction: column;
	justify-content: flex-start;
	align-items: center;
	margin: 0px;
	width: 100%;
	//height: 100vh;
}
.backButtonContainer {
	z-index: 1;
	margin-top: 20px;
	margin-left: 30px;
	display: flex;
	align-self: flex-start;
	justify-content: flex-start;
	align-items: center;
	align-content: center;
}
.backButton {
	cursor: pointer;
	background-color: transparent;
	border: none;
	outline: none;
}
.backButtonIcon {
	width: 24px;
	height: 24px;
}
.inviteColleguesContainer {
	// z-index: 2;
	position: absolute;
	top: 36.1%;
	left: 0px;
	display: flex;
	flex: 0.1;
	height: 72px;
	width: 72px;
	border-radius: 0 24px 24px 0;
	background-color: #ffffff;
	box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}
.inviteColleguesButton {
	border: none;
	outline: none;
	cursor: pointer;
	margin: 20px;
	height: 40px;
	width: 40px;
	border-radius: 8px;
	background-color: #ffffff;
	display: flex;
	justify-content: center;
	align-items: center;
	
}
.inviteColleguesButtonIcon {
	height: 24px;
	width: 18.37px; 
}
.controlsContainer {
	//z-index: 2;
	position: absolute;
	top: 49.1%;
	left: 0px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	align-items: center;
	flex: 0.1;

	height: 128px;
	width: 72px;
	border-radius: 0 24px 24px 0;
	background-color: #f1f1f1;
}
.addPositionButton {
	border: none;
	outline: none;
	cursor: pointer;
	margin: 16px;
	height: 40px;
	width: 40px;
	border-radius: 8px;
	background-color: #ffffff;
	display: flex;
	justify-content: center;
	align-items: center;
}
.addPositionButtonIcon {
	height: 20px;
	width: 17px;
	color:var(--app-color-gray-light);
}
.defineDepartmentsButton {
	border: none;
	outline: none;
	cursor: pointer;
	margin: 16px;
	height: 40px;
	width: 40px;
	border-radius: 8px;
	background-color: #ffffff;
	display: flex;
	justify-content: center;
	align-items: center;
}
.defineDepartmentsButtonIcon {
	height: 20px;
	width: 20px;
} 

.chartContainer {
	position: relative;
	flex: 1;
	margin: 72px 0px 0px 72px;
	display: block;
	overflow: auto;
	width: 92vw;
	height: calc(100% - 10px);
}


.paragraphNameContainer {
    display: flex;
	position:absolute;
	left: 0;
    margin: 100px 40px 40px 40px;	
    p {
        color: var(--app-color-gray-dark);
        font-family: var(--app-text-main-font), serif;
        font-size: 24px; 		   
    }
}
